import React from 'react';
import get from 'lodash/get';
import Helmet from 'react-helmet';
import Blogs from '../components/Blogs';
import Sidebar from '../components/Sidebar';
import Baner from '../components/Baner';
import { rhythm } from '../utils/typography';
import { BLOG_TYPES } from '../constants/constants';
import './style.css';

const adsenseCode = '\
(adsbygoogle = window.adsbygoogle || []).push({\
  google_ad_client: "ca-pub-6001220774327712",\
  enable_page_level_ads: true\
});\
'

const PAGE_SIZE = 9;
const isSeleced = (filter, source) =>
  filter === source ? 'index-filter-selected' : '';

class BlogIndex extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filter: '',
      page: 1,
    };
  }

  renderFilter(filter) {
    return (
      <button
        key={filter}
        className={isSeleced(this.state.filter, filter)}
        onClick={() => this.setState({ filter })}
      >
        {filter}
      </button>
    );
  }

  renderFilters() {
    const { filter } = this.state;
    return (
      <nav className="index-filters">
        {BLOG_TYPES.map(x => this.renderFilter(x))}
      </nav>
    );
  }

  renderMore(posts) {
    if (posts.length < this.state.page * PAGE_SIZE) return false;

    return (
      <div className="index-more">
        <button
          onClick={() =>
            this.setState(preState => ({ page: preState.page + 1 }))
          }
        >
          
        </button>
      </div>
    );
  }

  render() {
    const { page } = this.state;
    const siteTitle = get(this, 'props.data.site.siteMetadata.title');
    const posts = get(this, 'props.data.allMarkdownRemark.edges')
      .filter(x => {
        const { filter } = this.state;
        if (filter === '') return true;
        return x.node.frontmatter.catalog === filter;
      })
      .slice(0, PAGE_SIZE * page);
    return (
      <div className="index">
        <Helmet>
          <title>{siteTitle}</title>
          <meta name="baidu-site-verification" content="u4QzOXOJ0t" />
          <script async src="//pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
          <script>{adsenseCode}</script>
        </Helmet>
        <div className="index-baner">
          <Baner />
        </div>
        {this.renderFilters()}
        <div className="index-main">
          <div className="index-blogs">
            <Blogs blogs={posts} />
            {this.renderMore(posts)}
          </div>
          <div className="index-sidebar">
            <Sidebar />
          </div>
        </div>
      </div>
    );
  }
}

export default BlogIndex;

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }, filter: { frontmatter: { publish: { eq: true } } }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            title
            author
            catalog
            publish
          }
        }
      }
    }
  }
`;



