import React, { Component } from 'react';
import Link from 'gatsby-link';
import { hots } from '../../constants/hots';
import left from './images/left.svg';
import right from './images/right.svg';
import './style.css';

class Baner extends Component {
  constructor(props) {
    super(props);
    this.interval = null;
    this.state = {
      blogs: hots.slice(0, 3),
      count: 1,
      banerStatus: true,
    };
  }

  componentDidMount() {
    this.startChangeTimer();
  }

  changeBaner(direction) {
    this.setState(preState => {
      const { count } = preState;
      if (direction === '+') {
        return {
          count: count >= hots.length ? 1 : count + 1,
          blogs: hots.concat(hots.slice(0, 3)).slice(count, count + 3),
        };
      }
      return {
        count: count <= 1 ? hots.length : count - 1,
        blogs: hots.concat(hots.slice(0, 3)).slice(count, count + 3),
      };
    });
  }

  componentWillUnmount() {
    this.clearChangeTimer();
  }

  startChangeTimer = () => {
    if (this.interval) return;
    this.interval = setInterval(() => {
      if (this.state.banerStatus) {
        this.changeBaner('+');
      }
    }, 5000);
  }

  clearChangeTimer = () => {
    if (!this.interval) return;
    clearInterval(this.interval);
    this.interval = null;
  }

  changeBanerTimerStatus = () => {
    if (!this.interval) return;
    this.setState({ banerStatus: !this.state.banerStatus })
  }

  render() {
    const { blogs } = this.state;
    return (
      <div
        className="baner"
        onMouseEnter={this.changeBanerTimerStatus}
        onMouseLeave={this.changeBanerTimerStatus}
      >
        {blogs.map((x, index) => (
          <div key={x.node.fields.slug} className="baner-item">
            <Link to={x.node.fields.slug}>
              <img
                src={require(`../../pages${x.node.fields.slug}header.jpeg`)}
              />
            </Link>
            {index !== 0 ? (
              false
            ) : (
              <button
                className="baner-left"
                onClick={() => this.changeBaner('-')}
              >
                <img src={left} />
              </button>
            )}
            {index !== 0 ? (
              false
            ) : (
              <button
                className="baner-right"
                onClick={() => this.changeBaner('+')}
              >
                <img src={right} />
              </button>
            )}
            <header>
              <span>{x.node.frontmatter.title}</span>
            </header>
          </div>
        ))}
      </div>
    );
  }
}

export default Baner;



