import React, { Component } from 'react';
import Link from 'gatsby-link';
import BlogHeader from '../BlogHeader';
import './style.css';

const Blog = props => (
  <Link to={props.link} className="blog">
    <img src={props.img} />
    <header>
      <span>{props.title}</span>
    </header>
    <BlogHeader {...props} />
    <p dangerouslySetInnerHTML={{ __html: props.intro }} />
  </Link>
);

export default Blog;



